import {
    Point3, LinePoint, LineVec, SplineCurve, Surface,
    Vector3, multiply, add,
} from '../NRenderer/Geometry';

export class Scene {
    public t = 0.0;
    public spline: SplineCurve;

    constructor() {

        const ps: Point3[] = []
        const vs: Vector3[] = []

        let v = new Vector3(-3, -3, 0)
        ps.push(new Point3(v));
        vs.push(v);

        v = new Vector3(+3, -3, 0)
        ps.push(new Point3(v));
        vs.push(v);

        v = new Vector3(+3, +5, 5)
        ps.push(new Point3(v));
        vs.push(v);

        v = new Vector3(-3, +5, 5)
        ps.push(new Point3(v));
        vs.push(v);

        v = new Vector3(-3, -5, 2)
        ps.push(new Point3(v));
        vs.push(v);

        this.spline = new SplineCurve(vs);

    }
    public update() {


        this.t += 0.01;
    }
}
